"use client"

import { useEffect, useRef, useState } from "react";
import Card from "./Card";
import ModalVerifyOtp from "./modal/ModalVerifyOtp";
import { AnimatePresence, motion } from "framer-motion";
import TextInput from "./TextInput";
import BrandMark from "./BrandMark";
import Ripple from "material-ripple-effects";
import axiosApi from "@/config/axios-api";
import { addToast } from "@heroui/toast";
import PopUpMessage from "./modal/PopUpMessage";
import { cryptoEncode } from "@/config/crypto.mjs";

export default function LoginCard({ animated }) {
  const [showPass, setShowPass] = useState(false);
  const [token, setToken] = useState("");
  const [input, setInput] = useState({ username: "", password: "" });
  const [showModalOtp, setShowModalOtp] = useState(false);
  const [loading, setLoading] = useState(false);
  const [hname, setHname] = useState("ชื่อโรงพยาบาล");
  const [hcode, setHcode] = useState("รหัสโรงพยาบาล");
  const formRef = useRef(null);
  const abortRef = useRef(null);

  const [showModalPopUpMessage, setShowModalPopUpMessage] = useState(false);
  const [ popUpMessage, setPopUpMessage ] = useState("");

  const ripple = new Ripple();

  useEffect(() => {
    abortRef.current = new AbortController();
    (async () => {
      try {
        const res = await fetch("/mra/config.json");
        const data = await res.json();
        if (res.status === 200) {
          setHname(data.find((c) => c.HOSPITAL_NAME)?.HOSPITAL_NAME);
          setHcode(data.find((c) => c.HOSPITAL_CODE)?.HOSPITAL_CODE);
        }
      } catch (err) {
        // ไม่ร้องก็ได้ แค่เผื่อไม่ให้หน้าพังถ้าเน็ตหาย
        console.log("fetchHospitalInfo:", err?.name || err);
      }
    })();
    return () => abortRef.current?.abort();
  }, []);

  const handleSubmit = async (e) => {
    e?.preventDefault();
    setLoading(true);
    const data = {
      username: input.username,
      password: input.password,
    }

    const hashData = await cryptoEncode(JSON.stringify(data));
    try {
      const res = await axiosApi.post("/auth/login", {
        username: input.username,
        password: input.password,
      })

      if (res.status === 200) {
        setToken(res.data.token);
        localStorage.setItem("pre-verify", hashData);
        setShowModalOtp(true);
      };
    } catch (err) {
      setShowModalPopUpMessage(true);
      setPopUpMessage(err.response.data.message);
      addToast({
        title: "เกิดข้อผิดพลาด",
        description: err.response.data.message,
        timeout: 5000,
        color: "danger",
        shouldShowTimeoutProgress: true,
      })
    } finally {
      setLoading(false);
    }
  }

  return (
    <Card>
      <AnimatePresence initial={false} mode="wait">
        { showModalPopUpMessage && <PopUpMessage title="เกิดข้อผิดพลาด" type="error" message={popUpMessage || `ระบบเกิดข้อผิดพลาดอยู่ในขณะนี้…`} onClose={() => setShowModalPopUpMessage(false)} /> }
      </AnimatePresence>
      <ModalVerifyOtp open={showModalOtp} onClose={() => setShowModalOtp(false)} token={token} onResend={(e) => handleSubmit(e)} />
      <motion.div initial="hidden" animate="visible" transition={{ staggerChildren: 0.25, delayChildren: 0.15 }}>
        {/* Header */}
        <motion.div className="flex flex-col items-center gap-3 px-6 pt-6 text-zinc-800 dark:text-zinc-100" variants={animated.fadeUp}>
          <BrandMark />
          <div className="text-center">
            <h2 className="text-lg font-semibold">Medical Record Audit</h2>
            <p className="text-xs text-zinc-500 dark:text-zinc-400 mt-1">
              {hname} <span className="text-zinc-400 dark:text-zinc-500">({hcode})</span>
            </p>
          </div>
        </motion.div>

        {/* Form */}
        <motion.form ref={formRef} className="px-6 pb-6 pt-4" onSubmit={handleSubmit} variants={animated.fadeUp}>
          <label htmlFor="username" className="mb-2 block text-sm text-zinc-700 dark:text-zinc-200">
            ชื่อผู้ใช้
          </label>
          <TextInput id="username" name="username" autoComplete="username" placeholder="เช่น staff001" onChange={e => setInput({ ...input, username: e.target.value })} required />

          <label htmlFor="password" className="mb-2 mt-4 block text-sm text-zinc-700 dark:text-zinc-200">
            รหัสผ่าน
          </label>
          <TextInput
            id="password"
            name="password"
            type={showPass ? "text" : "password"}
            autoComplete="current-password"
            placeholder="••••••••"
            onChange={e => setInput({ ...input, password: e.target.value })}
            required
          />

          {/* แสดงรหัสผ่าน + ไมโครแอนิเมชัน */}
          <motion.button
            type="button"
            onClick={() => setShowPass(v => !v)}
            className="mt-3 inline-flex items-center gap-2 text-sm text-zinc-700 dark:text-zinc-200 select-none cursor-pointer"
            whileTap={{ scale: 0.98 }}
          >
            <span
              className={[
                "grid place-items-center size-5 rounded-full border",
                showPass
                  ? "bg-sky-500 border-sky-500"
                  : "border-zinc-400 dark:border-zinc-600 bg-transparent",
              ].join(" ")}
            >
              <motion.svg
                viewBox="0 0 24 24"
                className="w-3.5 h-3.5 text-white"
                initial={false}
                animate={{ scale: showPass ? 1 : 0, opacity: showPass ? 1 : 0 }}
                transition={{ type: "spring", stiffness: 500, damping: 30 }}
              >
                <path
                  d="M5 13l4 4L19 7"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="3"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </motion.svg>
            </span>
            แสดงรหัสผ่าน
          </motion.button>

          {/* ปุ่ม */}
          <motion.button
            type="submit"
            onMouseUp={e => ripple.create(e)}
            className={[
              "relative mt-5 w-full rounded-lg px-4 py-2.5 text-sm font-bold cursor-pointer select-none",
              "bg-zinc-300 hover:bg-zinc-200",
              "dark:bg-zinc-800 dark:hover:bg-zinc-700",
              "disabled:opacity-50 disabled:cursor-not-allowed",
            ].join(" ")}
            whileTap={{ scale: 0.99 }}
            disabled={loading || showModalPopUpMessage}
          >
            {(loading || showModalPopUpMessage) ? "กำลังเข้าสู่ระบบ" : "เข้าสู่ระบบ"}
          </motion.button>

          <motion.div className="mt-3 text-center" variants={animated.fadeUp}>
            <span className="text-[10px] text-zinc-500 dark:text-zinc-400">
              กลุ่มงานสุขภาพดิจิทัล
            </span>
          </motion.div>
        </motion.form>
      </motion.div>
    </Card>
  );
}